.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 16px;
  display: flex;
}

.card-img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-bottom: 40px;
}

.card-button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.title-product {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-style: normal;
  line-height: 14px;
}

.review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.price {
  color: #060b35;
  margin: 0;
  font-weight: 700;
  line-height: 18px;
}

.price-content {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
}

.stars {
  display: flex;
}

.stars-review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 4px;
}

body {
  width: 200px;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
}

